$white: #f5f5f5;
$dark: #2c3e50;
$lighter: #f7f0ea;
$lightest: #496886;
$light: #435f7a;
$red: #e74c3c;
$green: #2ecc71;
$yellow: #f1c40f;
$gray: #95a5a6;
$break: 767px;
$break-big: 991px;
$bg-aside: #EAF2FF;

@mixin transition($transition...) {
	-moz-transition:    $transition;
	-o-transition:      $transition;
	-webkit-transition: $transition;
	transition:         $transition;
}
*, ::after, ::before {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}
html {
	margin: 0;
	padding: 0;
}
body {
	margin: 0;
	padding: 0;
	height: 100%;
	background: transparent;
}
p {
	margin-top: 0;
	margin-bottom: 15px;
}
img {
	max-width: 100%;
	height: auto;
	vertical-align: middle;
	border-style: none;
}
dl, ol, ul {
	margin-top: 0;
	margin-bottom: 1rem;
	list-style: none;
	padding: 0;
}

.dropdown-menu {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;
    display: none;
    float: left;
    min-width: 10rem;
    padding: .5rem 0;
    margin: .125rem 0 0;
    font-size: 1rem;
    color: #212529;
    text-align: left;
    list-style: none;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.15);
    border-radius: .25rem;
}

.popup-chat-bot {
	// display: none;
	position: fixed;
	z-index: 999;
	width: 100%;
	max-width: 580px;
	height: auto;
	padding: 12px 10px 0;
	//top: 0;
	right: 0;
	bottom: 0;
	font-family: 'Arial', sans-serif;
	font-style: normal;
	font-size: 14px;
	.popup-inner {
		background: linear-gradient(272.4deg, #5B8C51 0%, #A5CD39 100%);
		box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
		border-radius: 5px 5px 0px 0px;
	}

	.pull-right {
		float: right;
	}
	.pull-left {
		float: left;
	}


