	.popup-content {
		position: relative;
		display: flex;
		flex-direction: column;
		height: 485px;
		.popup-messages {

			position: relative;
			display: flex;
			flex-direction: column;
			width: 100%;
			height: 100%;
			overflow-x: hidden;
			z-index: 2;
			background: #fff none repeat scroll 0 0;
			.direct-chat-messages {
			    height: auto;
			    overflow: auto;
			    padding: 10px;
			    transform: translate(0px, 0px);

				ul {
					position: relative;
					display: block;
					margin: 0;
					padding: 0;
					overflow: hidden;
					li {
						position: relative;
						display: inline-block;
						clear: both;
						float: left;
						margin: 15px 0px 5px 0px;
						width: 100%;
						max-width: calc(100% - 55px);
						font-size: inherit;
						
						&:nth-last-child(1) {
							margin-bottom: 20px;
						}
						&.height_ul {
							display: block;
							position: absolute;
							width: 100%;
							max-width: 100%;
							height: 100%;
							margin: 0;
							padding: 0;
							overflow: hidden;
						}
						img {
							width: 30px;
							height: 30px;
							border-radius: 50%;
							float: left;
							object-fit: scale-down;
						}
						
						p {
							display: inline-block;
							min-width: 55px;
							max-width: calc(100% - 38px);
							padding: 10px 15px;
							border-radius: 20px;
							line-height: 130%;
						}
						
						&.replies {
							float: left;
							clear: both;
							.item-inner {
								position: relative;
								float: left;
								width: 100%;
								.at-time {
									position: absolute;
									left: 38px;
									bottom: 0;
									font-size: 12px;
									color: #A7A7A7;
									white-space: nowrap;
								}
							}
							img {
								float: left;
								margin: 6px 8px 0 0;
							}
							
							p {
								background: #F7F0EA;
								color: #404A3D;
								white-space: pre-wrap;
							}
						}
						
						&.sent {
							float: right;
							clear: both;
							.item-inner {
								position: relative;
								float: right;
								width: 100%;
								.at-time {
									position: absolute;
									right: 5px;
									bottom: 0;
									font-size: 12px;
									color: #A7A7A7;
									text-align: right;
								}
							}
							img {
								float: right;
								margin: 6px 0 0 8px;
								display: none;
							}
							
							p {
								background: rgba(165, 205, 57, 0.5);
								color: #404A3D;
								float: right;
								white-space: pre-wrap;
							}
						}
					}
					.break-line {
						display: block;
						max-width: calc(100% - 78px);
						height: 22px;
						margin: 15px auto 10px;
						text-align: center;
						float: none;
						.line {
							display: inline-block;
							position: absolute;
							left: 0;
							right: 0;
							top: 0;
							bottom: 0;
							margin: auto;
							height: 1px;
							width: 100%;
							background: #e9e9e9;
						}
						.line-time {
							position: absolute;
							display: table;
							top: 0;
							left: 50%;
							transform: translateX(-50%);
							height: 22px;
							padding: 0 20px;
							line-height: 22px;
							font-size: 12px;
							border-radius: 10px;
							background-color: #f5f5f5;
							color: #435f7a;
							white-space: nowrap;
						}
					}
				}
			}
		}


		.popup-messages-footer {
			position: relative;
			width: 100%;
			min-height: inherit;
			border-top: 1px solid rgba(0, 0, 0, .1);
			background: #fff none repeat scroll 0 0;
			& > textarea {
				position: relative;
				width: 100%;
				max-height: 100px;
				min-height: 32px;
				padding: 8px 15px 5px;
				font-family: inherit;
				font-size: 14px;
				line-height: 1.4;
				overflow-x: hidden;
				overflow-y: auto;
				color: #616161;
				cursor: text;
				border: 0;
				//border-bottom: 1px solid #b2b2b2;
				outline: none;
				resize: none;
				vertical-align: top;
				word-break:break-word;
				white-space:pre-wrap;
				&:focus {
					box-shadow: 0px 0.5px 0.1px rgba(229, 229, 229, 0.5);
				}
				&::placeholder {
					color: #B4B4B4;
				}
			}
			.btn-footer {
				padding: 5px 5px 6px 15px;
				width: 100%;
				vertical-align: top;
				overflow: hidden;
				.btn-icon {
					position: relative;
					padding: 0;
					margin-right: 5px;
					background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
					border: medium none;
					outline: none;
					i {
						width: 30px;
						height: 30px;
						line-height: 28px;
						border-radius: 50%;
						font-size: 16px;
						color: #939393;
						//border: 1px solid #d5d5d5;
					}
					&:hover {
						i {
							color: #5B8C51;
							//border: 1px solid #ff6701;
							fill: #5B8C51;
						}
					}
					&.chat_submit {
						margin-right: 10px;
						&:before, &:after{
							position: absolute;
							left: 50%;
							top: 50%;
							transform: translateX(-50%) translateY(-50%);
							animation: pulse 2s ease-in-out infinite;
							border-radius: 30px;
							content: '';
							height: 5px;
							width: 5px;
						}
					}
				}
				.icon-camera {
					display: block;
					width: 100%;
					height: 100%;
