	@keyframes pulse {
	  0% {
	    box-shadow: 0px 0px 0px 0px rgba(#A5CD39, .5);
	  }
	  100% {
	    box-shadow: 0px 0px 0px 10px rgba(#A5CD39, 0);
	  }
	}

	@keyframes pulse-letter {
	  from, to{
	    color: rgb(220,220,220);
	  }
	  50%{
	    color: #A5CD39;
	  }
	}

