					border-bottom: 1px solid #E5E5E5;
					cursor: pointer;
					@media screen and (max-width: $break) {
						padding: 6px 0 46px 8px;
					}
					
					&:hover {
						background: #f7f0ea;
					}
					
					&.active {
						background: #f7f0ea;
						pointer-events: none;
						// border-right: 5px solid #5c8d50;
						&:before {
							content: "";
							position: absolute;
							display: block;
							top: 0;
							left: -10px;
							height: 100%;
							width: 3px;
							background: #5c8d50;
						}
						
						span.contact-status {
							border: 2px solid $lighter !important;
						}
					}
					
					.wrap {
						position: relative;
						width: calc(100% - 70px);
						margin: 0;
						@media screen and (max-width: $break) {
							width: 100%;
						}
						
						span {
							position: absolute;
							left: 0;
							margin: -2px 0 0 -2px;
							width: 10px;
							height: 10px;
							border-radius: 50%;
							border: 2px solid #646c61;
							background: $gray;

							&.online {
								background: $green;
							}
							
							&.away {
								background: $yellow;
							}
							
							&.busy {
								background: $red;
							}
						}

