							width: 40px;
							height: 40px;
							border-radius: 0;
							float: left;
							margin-right: 10px;
							border: 1px solid #F7F0EA;
							@media screen and (max-width: $break) {
								margin-right: 0px;
							}
						}

						.meta {
							padding: 3px 0 0 0;
							color: #404A3D;
							@media screen and (max-width: $break) {
								display: none;
							}

							.name {
								margin: 0;
								font-size: 14px;
								font-weight: bold;
								white-space: nowrap;
								overflow: hidden;
								text-overflow: ellipsis;
							}

							.preview {
								margin: 2px 0 0 0;
								padding: 0 0 1px;
								font-size: 12px;
								white-space: nowrap;
								overflow: hidden;
								text-overflow: ellipsis;
								@include transition(1s all ease);
								
								span {
									position: initial;
									border-radius: initial;
									background: none;
									border: none;
									padding: 0 2px 0 0;
									margin: 0 0 0 1px;
									opacity: .5;
								}
							}
						}
						.recent-time {
							position: absolute;
							right: -70px;
							top: 5px;
							font-size: 12px;
							color: #A7A7A7;
							white-space: nowrap;
							@media screen and (max-width: $break) {
								display: none;
							}
						}
					}
				}
			}
		}

	}

	@keyframes pulse {
	  0% {
	    box-shadow: 0px 0px 0px 0px rgba(#A5CD39, .5);
	  }
	  100% {
	    box-shadow: 0px 0px 0px 10px rgba(#A5CD39, 0);
	  }
	}

	@keyframes pulse-letter {
	  from, to{
	    color: rgb(220,220,220);
	  }
	  50%{
	    color: #A5CD39;
	  }
	}

}